<template>
  <div class="forgetpwd">
    <!-- 导航 -->
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <!-- 内容 -->
    <div class="content">
      <div class="content-bg">
        <div class="content-box">
          <div class="content-list">
            <div class="title">{{ $t('bindphone.title') }}</div>
            <div class="account">
              <el-form ref="loginForm" :model="form">
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('bindphone.phone_text') }}</el-row>
                  <el-input
                    class="el-input-row"
                    v-model="form.phone"
                    type="number"
                    auto-complete="off"
                  ></el-input>
                </el-form-item>
                <el-form-item prop="checkPass">
                  <el-row class="text">{{ $t('bindmpwd.code_text') }}</el-row>
                  <el-row class="el-input-border" style="display: flex;">
                    <el-input
                      class="el-input-row"
                      v-model="form.code"
                      type="text"
                      auto-complete="off"
                    ></el-input>
                    <div class="el-input-choose el-input-choose-code">
                      <div class="el-value" @click="getCode">{{ btn_get_code }}</div>
                    </div>
                  </el-row>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="save">{{ $t('bindmpwd.bindmpwd_btn') }}</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getCode } from '@/api/index'
import { saveTpwd } from '@/api/user'
const inter = 60

export default {
  components: {
    AoNav
  },
  data() {
    return {
      btn_get_code2: null,
      btn_get_code: '',
      code_unit: '',
      sendCodeFlag: false,
      form: {
        phone: "",
        code: ''
      }
    }
  },
  mounted() {
    this.btn_get_code2 = this.$t('bindmpwd.code_btn')
    this.btn_get_code = this.btn_get_code2
    this.code_unit = this.$t('bindmpwd.code_unit')
  },
  methods: {
    error(key) {
      this.$message({ message: this.$t('bindmpwd.'+key), type: 'error' })
    },
    save() {
      if (!this.form.phone) {
        this.error('phone_no_data')
        return
      }

      if (!this.form.code) {
        this.error('code_no_data')
        return
      }
      if (this.r_code == 0 || this.r_code != this.form.code) {
        this.error('code_no_equality')
        return
      }
      saveTpwd(this.form).then(res => {
        this.$store.dispatch('updateInfo', { phone: this.form.phone })
        this.$router.back(-1)
      })
    },
    getCode() {
      if(this.sendCodeFlag) return
      this.sendCodeFlag = true
      getCode({ type: 'bindphone', account: this.form.phone }).then(({ code = 0 }) => {
        this.r_code = code
        this.btn_get_code = inter + this.code_unit
        var _inter = inter
        var i = setInterval(() => {
          _inter --
          this.btn_get_code = _inter + this.code_unit
          if (_inter === 0) {
            clearInterval(i)
            this.btn_get_code = this.btn_get_code2
            this.sendCodeFlag = false
          }
        }, 1000)
      }).catch(() => {
        this.sendCodeFlag = false
      })
    },
  }
};
</script>

<style lang="scss">
.forgetpwd {
  .content {
    background-color: #1a243b;
  }
  .title {
    font-size: 30px;
    color: #c7cce6;
  }
  .content-bg {
    background: url(/static/image/bg_login.png) center bottom 316px repeat-x,
      -webkit-linear-gradient(top, #21263f, #171c2b);
  }
  .content-box {
    width: 1200px;
    // margin: 0 auto;
    padding-top: 93px;
    overflow: hidden;
    min-height: 880px;
  }
  .content-list {
    position: relative;
    padding: 0 0 60px 30px;
    .account {
      display: flex;
    }
    .account .el-form {
      width: 520px;
      .text {
        display: block;
        height: 22px;
        color: #61688a;
        font-size: 12px;
      }
      .xy {
        color: #7a98f7;
        margin-left: 5px;
        font-size: 12px;
        cursor: pointer;
      }
      .el-checkbox {
        .el-checkbox__inner {
          background-color: #1e2235;
          border-color: #c7cce6;
        }
        .el-checkbox__label {
          color: #c7cce6;
        }
      }
      .xy {
      }
      .el-form-item {
        margin-bottom: 8px;
      }
      .el-input-border {
        border: 1px solid #4e5b85;
        border-radius: 3px;
        position: relative;
        margin-top: 8px;
        .el-input-row {
          margin-top: 0;
          border: none;
        }
        .el-list-box {
          width: 100%;
          position: absolute;
          z-index: 5;
          left: 0;
          right: 0;
          top: 52px;
          height: 250px;
          overflow-y: auto;
          box-shadow: 0 2px 6px 0 #000;
          border-color: #d1d3df;
          background-color: #171c2b;
          color: #9194a4;
        }
        .el-list-box-item {
          padding: 15px;
          border-bottom: 1px solid #303b4b;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
      }
      .el-input-choose {
        display: flex;
        width: 146px;
        align-items: center;
        background-color: #1e2235;
        border-right: 1px solid #4e5b85;
        cursor: pointer;
        &.el-input-choose-code {
          border-left: 1px solid #4e5b85;
          border-right: none;
          text-align: center;
        }
        .el-value {
          flex: 1;
          text-indent: 0.5rem;
        }
        .el-value,
        .el-icon {
          width: 2.6rem;
          color: #c7cce6;
          font-size: 14px;
        }
      }
      .el-input-row {
        width: 520px;
        min-height: 46px;
        border: 1px solid #4e5b85;
        color: #c7cce6;
        font-size: 14px;
        border-radius: 3px;
        background-color: #1e2235;
        position: relative;
        margin-top: 8px;
      }
      .el-input__inner {
        border: none;
        height: 44px;
        padding-left: 10px;
        padding-right: 10px;
        background: transparent;
        color: #c7cce6;
      }
      .el-button {
        flex: 1;
        width: 200px;
        display: inline-block;
        line-height: 46px;
        background-color: #7a98f7;
        border-radius: 4px;
        color: #fff;
        border: none;
        padding: 0;
      }
      .forgetpassword {
        font-size: 14px;
        color: #7a98f7;
        cursor: pointer;
        margin-left: 1rem;
      }
    }
  }
}
</style>



